<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app'
}

</script>

<style>
body{
  padding:0px;
  margin:0px auto;
}
a{
  text-decoration:none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
}
.icon-static{
  /*  font-size  */
  width: 1em; height: 1em;
  /*  */
  vertical-align: -0.15em;
  /*  color  SVG /fill */
  fill: currentColor;
  /* path  stroke  viewBox  IE 
     normalize.css  */
  overflow: hidden;

  font-size: 16px;
  line-height: 100px;
  color:#fff;
  -webkit-transition: font-size 0.25s ease-out 0s;
  -moz-transition: font-size 0.25s ease-out 0s;
  transition: font-size 0.25s ease-out 0s;
}
.icon {
  /*  font-size  */
  width: 1em; height: 1em;
  /*  */
  vertical-align: -0.15em;
  /*  color  SVG /fill */
  fill: currentColor;
  /* path  stroke  viewBox  IE 
     normalize.css  */
  overflow: hidden;

  font-size: 16px;
  line-height: 100px;
  margin: 0px 4px;
  color:#fff;
  -webkit-transition: font-size 0.25s ease-out 0s;
  -moz-transition: font-size 0.25s ease-out 0s;
  transition: font-size 0.25s ease-out 0s;
}
.icon:hover{
  cursor: pointer;
  font-size: 18px;
}


</style>



