import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Dashboard from '@/components/Dashboard'

import UserList from '@/components/user/list'
// import User from '@/components/user/user'
import Group from '@/components/user/group'

import UserChangePwd from '@/components/selfinfo/changepwd'
import UserProfile from '@/components/selfinfo/profile'

import Task from '@/components/task/task'

import Policy from '@/components/policy/policy'

import Volume from '@/components/volume/volume'

import Worker from '@/components/worker/worker'

import Log from '@/components/log/log'

// 
const Login = resolve => require(['@/components/Login'], resolve)

Vue.use(Router)

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/login',
      name: '',
      component: Login
    },
    {
      path: '/',
      name: 'home',
      component: Home,
      redirect: '/dashboard',
      leaf: true, // 
      menuShow: true,
      isSuperAdm: false,
      iconCls: '#icon-home', // class
      children: [
        {path: '/dashboard', component: Dashboard, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      isSuperAdm: false,
      leaf: true, // 
      iconCls: '#icon-task', // class
      children: [
        {path: '/task', component: Task, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      isSuperAdm: false,
      leaf: true, // 
      //iconCls: 'iconfont icon-policy', // class
      iconCls: '#icon-policy',
      children: [
        {path: '/policy', component: Policy, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      isSuperAdm: false,
      leaf: true, // 
      iconCls: '#icon-host', // class
      children: [
        {path: '/worker', component: Worker, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      isSuperAdm: false,
      leaf: true, // 
      iconCls: '#icon-volume', // class
      children: [
        {path: '/volume', component: Volume, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: '#icon-userMgr',
      isSuperAdm: true,
      children: [
        // {path: '/user', component: User, name: '', menuShow: true},
        {path: '/user/list', component: UserList, name: '',iconCls: '#icon-users', menuShow: true},
        {path: '/user/group', component: Group, name: '',iconCls: '#icon-groups', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: false,
      iconCls: '#icon-user',
      children: [
        // {path: '/user', component: User, name: '', menuShow: true},
        {path: '/selfinfo/profile', component: UserProfile, name: '', menuShow: true},
        {path: '/selfinfo/changepwd', component: UserChangePwd, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      isSuperAdm: false,
      leaf: true, // 
      iconCls: '#icon-log', // class
      children: [
        {path: '/log', component: Log, name: '', menuShow: true}
      ]
    },
  ]
})

router.beforeEach((to, from, next) => {
  // console.log('to:' + to.path)
  if (to.path.startsWith('/login')) {
    window.sessionStorage.removeItem('access-user')
    next()
  } else {
    let user = JSON.parse(window.sessionStorage.getItem('access-user'))
    if (!user) {
      next({path: '/login'})
    } else {
      next()
    }
  }
})

export default router



